import { css } from '@emotion/react';

import { PreventLargeWidthBox, Stack } from 'src/components/layout';
import {
  BestFitProductIntroPaper,
  BestFitProductLodingPaper,
  BestFitProductPaper,
} from 'src/components/papers';
import useProductInfo from 'src/hooks/useProductInfo';
import useSurveysForBestFit from 'src/hooks/useSurveysForBestFit';

// scroll-snap-points-y: repeat(100vh);
const scrollSnapTypeMandatory = css`
  overflow: scroll;
  scroll-snap-type: x mandatory;
`;

const BestFitProduct = (): JSX.Element => {
  const { name: productName } = useProductInfo();
  const { backgroundImage, surveyBackgroundImage, surveys } =
    useSurveysForBestFit({ productName });

  return (
    <PreventLargeWidthBox>
      <Stack
        direction="row"
        justifyContent="space-between"
        css={scrollSnapTypeMandatory}
      >
        <BestFitProductIntroPaper imageUrl={backgroundImage} />

        {surveys.map(({ question, options }, index) => {
          const key = `${index}-${question}`;
          return (
            <BestFitProductPaper
              key={key}
              imageUrl={surveyBackgroundImage}
              question={question}
              options={options}
            />
          );
        })}

        <BestFitProductLodingPaper imageUrl={backgroundImage} />
      </Stack>
    </PreventLargeWidthBox>
  );
};

export default BestFitProduct;
